import React from 'react'
import { Box, Heading, Button } from '@chakra-ui/react';
import { NavLink } from 'react-router-dom';

const Nav = () => {
    return (
      <Box  display={"flex"} 
            justifyContent={"space-between"} 
            alignItems={"center"}
            maxWidth={"1350px"}
            m={"auto"}
            pt={"1rem"}>
        
        <NavLink to={'/'}>
            <Heading width={"fit-content"} size={"lg"} textAlign={"center"}>
                BOOK STORE
            </Heading>
        </NavLink>
        
        <Box    width={"30%"} 
                display={"flex"} 
                justifyContent={"space-between"} 
                alignItems={"center"}>
            <NavLink to={"/"}>
                <Button borderRadius={"none"} colorScheme={"orange"}>
                    HOME
                </Button>
            </NavLink>

            <NavLink to={"/add"}>
                <Button borderRadius={"none"} colorScheme={"orange"}>
                    ADD PRODUCT
                </Button>
            </NavLink>

            <NavLink to={"/cart"}>
                <Button borderRadius={"none"} colorScheme={"orange"}>
                    CART
                </Button>
            </NavLink>
        </Box>
      </Box>
    )
}

export default Nav;