import React from 'react'
import { Box, Heading, Image, Button, useToast } from '@chakra-ui/react';
import { NavLink } from 'react-router-dom';

const Product = ({title, price, id, thumbnail}) => {
  const toast = useToast();

  const deleteProduct = async (id) => {
    let deleteProd = await fetch(`/api/products/${id}`, {
      method: 'DELETE'
    })
    let response = await deleteProd.json();
    toast({
      title: `${response} deleted successfully`,
      description: `You can checkout or keep browsing`,
      duration: 3000,
      isClosable: true,
    })
  }

  const addToCart = async () => {
    let product = {
      id: id, 
      title: title, 
      price: price, 
      img: thumbnail, 
      timestamp: new Date().toLocaleString()
    };

    let addProd = await fetch('/api/cart/0/products', {
      method: 'POST',
      headers: {
          'Content-Type': 'application/json'
      },
      body: JSON.stringify(product)
    })

    let response = await addProd.json();
    toast({
      title: `${response} added to cart`,
      description: `You can go to checkout or keep looking`,
      duration: 3000,
      isClosable: true,
    })

  }

    return (
      <Box  display={"flex"}
            flexDirection={"column"}
            justifyContent={"center"}
            alignItems={"start"}
            width={"200px"}
            padding={"1rem"}
            bg={"#363034"}>
        <Image m={"auto"} width={"150px"} height={"225px"} src={thumbnail}></Image>
        <Heading mt={"0.5rem"} size={"md"}>{title}</Heading>
        <Heading size={"md"} mb={"0.5rem"}>${price}</Heading>
        <Button onClick={addToCart} borderRadius={"none"} colorScheme={"orange"} width={"100%"} margin={"0.2rem"}>Add to cart</Button>
        <Box display={"flex"}>

            <NavLink to={`/book/${id}`}>
              <Button borderRadius={"none"} color={"#50B2C0"} variant={"ghost"} margin={"0.2rem"}>Update</Button>
            </NavLink>

            <Button onClick={() => deleteProduct(id)} borderRadius={"none"} color={"orange.500"} variant={"ghost"} margin={"0.2rem"}>Delete</Button> 

        </Box>
      </Box>
    )
}

export default Product;