import React from "react";
import { useParams } from "react-router-dom";
import { Box, FormErrorMessage, FormLabel, FormControl, Input, Button, Heading, useToast } from '@chakra-ui/react';
import { useForm } from 'react-hook-form';

const UpdateForm = () => {
    let id = useParams().id;
    const toast = useToast();
    const {
        handleSubmit,
        register,
        reset,
        formState: { errors },
    } = useForm()

    const onSubmit = async (data) => {
        data.price = parseInt(data.price);
        const sendData = await fetch(`/api/products/${id}`, {
            method: 'PUT',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(data)
        })
        const response = await sendData.json();
        
        toast({
            title: 'Book Updated Successfully!',
            description: `${response} was updated`,
            duration: 3000,
            isClosable: true,
          })
        reset();
    }
    return(
        <Box width={"60%"} margin={"auto"}>
            <form onSubmit={handleSubmit(onSubmit)}>
                <Heading size={"lg"} mt={"3rem"}>Update Product</Heading>
                <FormControl isRequired isInvalid={errors.name} padding={"1rem 0rem"}>
                    <FormLabel htmlFor='title'>New Title</FormLabel>
                    <Input
                        id='title'
                        placeholder='Title'
                        {...register('title', {
                            required: 'This field is required',
                            minLength: { value: 4, message: 'Minimum length should be 4' },
                        })}
                        // variant={"filled"} 
                        focusBorderColor={"#50B2C0"}
                        border={"1px solid #FEEFDD"}
                        bg={"#201E1F"}
                        color={"#50B2C0"}
                        borderRadius={"0px"}
                    // _placeholder={{color: colorMode === 'light' ? 'teal' : 'teal.300', opacity: "0.7"}}
                    />
                    <FormErrorMessage>
                        {errors.name && errors.name.message}
                    </FormErrorMessage>
                </FormControl>

                <FormControl isRequired isInvalid={errors.name} padding={"1rem 0rem"}>
                    <FormLabel htmlFor='price'>New Price</FormLabel>
                    <Input
                        id='price'
                        placeholder='Price'
                        type={"number"}
                        {...register('price', {
                            required: 'This field is required',
                            minLength: { value: 4, message: 'Minimum length should be 4' },
                        })}
                        // variant={"filled"} 
                        focusBorderColor={"#50B2C0"}
                        border={"1px solid #FEEFDD"}
                        bg={"#201E1F"}
                        color={"#50B2C0"}
                        borderRadius={"0px"}
                    // _placeholder={{color: colorMode === 'light' ? 'teal' : 'teal.300', opacity: "0.7"}}
                    />
                    <FormErrorMessage>
                        {errors.name && errors.name.message}
                    </FormErrorMessage>
                </FormControl>

                <FormControl isRequired isInvalid={errors.name} padding={"1rem 0rem"}>
                    <FormLabel htmlFor='img'>New Cover Image</FormLabel>
                    <Input
                        id='img'
                        placeholder='URL'
                        {...register('img', {
                            required: 'This field is required',
                            minLength: { value: 4, message: 'Minimum length should be 4' },
                        })}
                        // variant={"filled"} 
                        focusBorderColor={"#50B2C0"}
                        border={"1px solid #FEEFDD"}
                        bg={"#201E1F"}
                        color={"#50B2C0"}
                        borderRadius={"0px"}
                    // _placeholder={{color: colorMode === 'light' ? 'teal' : 'teal.300', opacity: "0.7"}}
                    />
                    <FormErrorMessage>
                        {errors.name && errors.name.message}
                    </FormErrorMessage>
                </FormControl>

                <Button colorScheme={"orange"} borderRadius={"none"} type="submit">
                    UPDATE
                </Button>
            </form>
        </Box>
    );
}

export default UpdateForm;