import React, { useEffect, useState } from "react";
import { Box, Heading } from "@chakra-ui/react";
import CartItem from "./CartItem";

const Cart = () => {

    const [items, setItems] = useState([]);

    useEffect( () => {
        fetch('/api/cart/0/products')
            .then(response => response.json())
            .then(data => {
                setItems(data);
            })
            .catch(error => console.error(error))
    }, [items]);

    return(
        <Box display={"flex"} 
            width={"100%"} 
            justifyContent={"start"} 
            alignItems={"center"}
            gap={"1rem"}
            flexWrap={"wrap"}
            maxWidth={"1350px"}
            m={"auto"}
            pt={"1rem"}>
            {items.map(b => 
                <CartItem   key={b.id} 
                            id={b.id} 
                            title={b.title} 
                            price={b.price}
                            img={b.img}
                            timestamp={b.timestamp}>
                </CartItem>)}
        </Box>
    )
}

export default Cart;